.header {
  background: var(--primary);
  flex-direction: column;
  align-items: center;
  display: flex;
  box-shadow: 0 4px 10px #0000001a;
}

.todo-item {
  max-width: 100%;
  background: #fff;
  border-radius: 12px;
  align-items: center;
  padding: 27px;
  display: flex;
  box-shadow: 0 6px 10px #0000001a;
}

.todo-item-title {
  color: #111;
  margin: 0;
  font-size: 18px;
  font-style: normal;
  font-weight: 500;
  line-height: 27px;
}

.todo-item-checkbox {
  appearance: none;
  box-sizing: border-box;
  width: 20px;
  height: 20px;
  background: #fff;
  border: 1px solid #c7c7c7;
}

.todo-item-checkbox:checked {
  background-color: var(--primary);
  background-image: url("checked.796636c8.svg");
  border: 0;
}

.todo-item-edit-button {
  margin-left: 16px;
}

.todo-item-edit-button span {
  width: 20px !important;
  height: 20px !important;
}

.todo-item-priority-indicator {
  width: 9px;
  height: 9px;
  border-radius: 50%;
  margin-left: 22px;
  margin-right: 16px;
}

.todo-item:has(.todo-item-checkbox:checked) .todo-item-title {
  color: #888;
  text-decoration-line: line-through;
}

.modal {
  width: 770px;
  height: 355px;
  max-width: 100%;
  background: #fff;
  border-radius: 12px;
  flex-direction: column;
  display: flex;
  box-shadow: 0 4px 10px #0000001a;
}

.modal-header {
  align-items: center;
  padding-bottom: 19px;
  display: flex;
  position: relative;
}

.modal-body {
  padding-top: 21px;
}

.modal-footer {
  align-items: center;
  margin-top: auto;
  padding-top: 15px;
  display: flex;
  position: relative;
  bottom: -5px;
}

.modal-header:after, .modal-footer:before {
  content: "";
  width: 100%;
  border: 1px solid #e5e5e5;
  padding-right: 58px;
  display: block;
  position: absolute;
  bottom: 0;
  left: -30px;
}

.modal-footer:before {
  bottom: unset;
  top: 0;
}

.modal-add-title {
  margin: 0;
  font-size: 18px;
  font-style: normal;
  font-weight: 600;
  line-height: 27px;
}

.custom-select-container {
  width: -moz-fit-content;
  width: fit-content;
}

.custom-select-container > .option-container {
  background: #fff;
  border-radius: 6px;
  display: none;
  position: absolute;
  box-shadow: 0 6px 15px 5px #0000001a;
}

.custom-select-container.type-select > .option-container {
  box-shadow: unset;
  z-index: 10;
}

.custom-select-container.type-button > .option-container li:first-child {
  border-radius: 6px 6px 0 0;
}

.custom-select-container .custom-select {
  width: 0;
  height: 0;
  appearance: none;
  border: 0;
  margin: 0;
  padding: 0;
  position: absolute;
}

.custom-select-container > .option-container.active {
  display: block;
}

.custom-select-toggle {
  border: 1px solid var(--alternative);
  box-sizing: border-box;
  background: #fff;
  border-radius: 6px;
  padding: 14px 18px;
  font-family: Poppins;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 24px;
}

.custom-select-toggle .priority-indicator {
  margin-right: 19px;
}

.custom-select-container:has(.option-container.active) .custom-select-toggle {
  border-bottom: 0;
  border-bottom-right-radius: 0;
  border-bottom-left-radius: 0;
}

.custom-select-toggle:after {
  content: "";
  width: 24px;
  height: 24px;
  vertical-align: middle;
  float: right;
  background-image: url("icon_chevron.56d68ab0.svg");
  background-size: contain;
  display: inline;
}

