.custom-select-container {
    width: fit-content;
}

.custom-select-container > .option-container {
    display: none;
    background: #FFFFFF;
    /* border: 1px solid #E5E5E5; */
    box-shadow: 0px 6px 15px 5px rgba(0, 0, 0, 0.1);
    border-radius: 6px;
    position: absolute;
}

.custom-select-container.type-select > .option-container {
    box-shadow: unset;
    z-index: 10;
}


.custom-select-container.type-button > .option-container li:first-child {
    border-radius: 6px 6px 0 0;   
}

.custom-select-container
    .custom-select {
    width: 0;
    height: 0;
    appearance: none;
    border: 0;
    margin: 0;
    padding: 0;
    position: absolute;
}

.custom-select-container > .option-container.active {
    display: block;
}

.custom-select-toggle {
    background: #FFFFFF;
    border: 1px solid var(--alternative);
    border-radius: 6px;
    font-family: 'Poppins';
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    padding: 14px 18px;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
}

.custom-select-toggle .priority-indicator {
    margin-right: 19px;
}

.custom-select-container:has(.option-container.active) .custom-select-toggle {
    border-bottom-left-radius: 0;
    border-bottom-right-radius: 0;
    border-bottom: 0;
}

.custom-select-toggle::after {
    content: '';
    background-image: url('/src/svg/icon_chevron.svg');
    background-size: contain;
    width: 24px;
    height: 24px;
    display: inline;
    vertical-align: middle;
    float: right;
}
