.modal {
    width: 770px;
    height: 355px;
    max-width: 100%;
    background: #FFFFFF;
    box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1);
    border-radius: 12px;
    display: flex;
    flex-direction: column;
}

.modal-header {
    display: flex;
    align-items: center;
    position: relative;
    padding-bottom: 19px;
}

.modal-body {
    padding-top: 21px;
}

.modal-footer {
    display: flex;
    align-items: center;
    position: relative;
    padding-top: 15px;
    margin-top: auto;
    bottom: -5px;
}

.modal-header:after, .modal-footer:before {
    content: '';
    width: 100%;
    display: block;
    border: 1px solid #E5E5E5;
    position: absolute;
    bottom: 0;
    left: -30px;
    padding-right: 58px;
}

.modal-footer:before {
    bottom: unset;
    top: 0;
}

.modal-add-title {
    font-style: normal;
    font-weight: 600;
    font-size: 18px;
    line-height: 27px;
    margin: 0;
}