.header {
    background: var(--primary);
    box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1);
    align-items: center;
    display: flex;
    flex-direction: column;
}

.todo-item {
    max-width: 100%;
    /* width: 1000px; */
    /* height: 80px; */
    background: #FFFFFF;
    box-shadow: 0px 6px 10px rgba(0, 0, 0, 0.1);
    border-radius: 12px;
    display: flex;
    align-items: center;
    padding: 27px;
}

.todo-item-title {
    margin: 0;
    font-style: normal;
    font-weight: 500;
    font-size: 18px;
    line-height: 27px;


    color: #111111;
}

.todo-item-checkbox {
    appearance: none;
    background-repeat: no-repeat;
    box-sizing: border-box;
    width: 20px;
    height: 20px;

    background: #FFFFFF;
    border: 1px solid #C7C7C7;
}

.todo-item-checkbox:checked {
    background-color: var(--primary);
    background-image: url(../svg/checked.svg);
    border: 0;
}

.todo-item-edit-button {
    margin-left: 16px;
}

.todo-item-edit-button span {
    width: 20px!important;
    height: 20px!important;
}

.todo-item-priority-indicator {
    margin-left: 22px;
    margin-right: 16px;
    width: 9px;
    height: 9px;
    border-radius: 50%;
}

.todo-item:has(.todo-item-checkbox:checked) .todo-item-title{
    text-decoration-line: line-through;
    color: #888888;
}
